    <h1>BELBINOV TEST</h1>
    <h3>Timske uloge</h3>
    <h3>Upitnik za sopstvenu procenu</h3>
    <p>Ovaj upitnik će ti pomoći da: </p>
    <ul>
      <li>Ovaj upitnik će ti pomoći da: </li>
      <li>Poboljšate uloge koje Vam najviše leže, da ih razjasnite, kako bi na najbolji način doprineli timskom radu, </li>
      <li>Postanete svestni da ne vredi “igrati uloge” koje Vam nisu bliske. Bolje je ulagati u sebe, poboljšati svoju ulogu u timu i odabrati tim ljudi sa kojima ćete se u zajedničkom radu dopunjavati. </li>
    </ul>

    <h3>UPITNIK</h3>
    <p>Za svaki od sedam delova upitnika na raspolaganju Vam stoji 10 bodova. Rasporedite ih na svaku od rečenica, ali tako da najveći broj bodova dodelite rečenici koja najtačnije opisuje Vaše ponašanje u grupi. Ukoliko samo jedna rečenica najtačnije oslikava Vašu ulogu u grupi – mozete za nju staviti svih 10 bodova.</p>
